import React from "react";

import L from 'leaflet';
import 'leaflet/dist/leaflet.css';

import { withStyles } from '@material-ui/core/styles';
import { mapStyles } from "./mapStyles";

import { Map, TileLayer, Marker, Popup } from 'react-leaflet';

var customIcon = L.icon({
  iconUrl: 'data:image/png;base64,',
  iconSize: [25, 41],
  iconAnchor: [12.5, 41],
  popupAnchor: [0, -41]
});

class MapComponent extends React.Component {
  state = {
    location: {
      lat: 45.41117,
      lng: -75.69812
    },
    haveLocation: false,
    zoom: 2,
  }

  componentDidMount() {
    navigator.geolocation.getCurrentPosition((position) => {
      this.setState({
        location: {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        },
        haveLocation: true,
        zoom: 13,
      });
      console.log(position);
    }, () => {
      // this else function can be altered later
      console.log('Location Unavailable');
      fetch('https://ipapi.co/json')
        .then(res => res.json())
        .then(response => {
          console.log(response)
          this.setState({
            location: {
              lat: response.latitude,
              lng: response.longitude
            },
            haveLocation: true,
            zoom: 13,
          });
        });
    });
  }

  render() {
    const { classes } = this.props;

    const local_position = [this.state.location.lat, this.state.location.lng]
    return (
      <Map className={classes.mapContent} center={local_position} zoom={this.state.zoom}>
        <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {
          this.state.haveLocation ?
            <Marker
              position={local_position}
              icon={customIcon}>
              <Popup>
                A pretty CSS3 popup. <br /> Easily customizable.
              </Popup>
            </Marker> : ''
        }
      </Map>
    )
  }
}

export default withStyles(mapStyles)(MapComponent);